/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lexend", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 3rem 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
}

.barra {
  display: none;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra__navegacion {
  display: flex;
  gap: 4rem;
}
.barra__link {
  color: #FFFFFF;
}
.barra__logo {
  margin: 0;
}
.barra__imagen {
  width: 10rem;
  height: auto;
}

.header {
  background-image: linear-gradient(rgba(10, 0, 0, 0.6) 0%, rgba(10, 0, 0, 0.6) 100%), url("../img/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  padding: 3rem 0 10rem 0;
}
.header__busqueda {
  margin-top: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header__busqueda {
    margin-top: 30rem;
    padding-bottom: 10rem;
  }
}
.header__busqueda-label {
  color: #e1e1e1;
}
.header__busqueda-boton {
  background-color: #FFFFFF;
  padding: 2rem 4rem;
  color: #e8505b;
  margin-top: 1rem;
  border-radius: 3rem;
  font-size: 2rem;
  font-weight: 700;
  display: block;
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .header__busqueda-boton {
    display: inline-block;
  }
}
.header__busqueda-boton:hover {
  background-color: #e8505b;
  color: #FFFFFF;
}

.formulario {
  margin: 5rem auto 2rem auto;
  background-color: #FFFFFF;
  border-radius: 5rem;
  box-shadow: 0px 3px 7px 0px #686868;
  padding: 3rem 0;
}
@media (min-width: 1024px) {
  .formulario {
    display: flex;
    justify-content: space-between;
    max-width: 90rem;
    padding: 0;
  }
}
.formulario__campo {
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .formulario__campo:nth-child(1) {
    padding-left: 4rem;
  }
}
.formulario__campo:hover {
  background-color: #f0f0f0;
  border-radius: 5rem;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.4);
}
.formulario__label {
  font-size: 1.2rem;
  font-weight: 900;
}
.formulario__input {
  border: none;
  width: 100%;
  color: #686868;
}
.formulario__submit {
  width: 100%;
  border: none;
  border-radius: 2rem;
  background-image: radial-gradient(circle, #e24680 0%, rgb(189, 30, 89) 100%);
  padding: 1rem 2rem;
  cursor: pointer;
  font-weight: 900;
  color: #FFFFFF;
  transition-property: background-image;
  transition-duration: 300ms;
}
.formulario__submit:hover {
  background-image: radial-gradient(circle, #e55c8f 0%, #911744 100%);
}

.descubre__grid {
  margin-top: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .descubre__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.descubre__heading {
  text-align: center;
}
@media (min-width: 768px) {
  .descubre__heading {
    text-align: left;
  }
}

.contenido {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
@media (min-width: 1024px) {
  .contenido {
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
}
.contenido__descripcion {
  width: 100%;
}
.contenido__imagen-contenedor {
  width: 100%;
}
.contenido__imagen {
  width: 20rem;
  height: auto;
  margin: 0 auto;
}
.contenido__heading {
  text-align: center;
}
@media (min-width: 1024px) {
  .contenido__heading {
    text-align: left;
  }
}
.contenido__texto {
  margin: 0;
  text-align: center;
}
@media (min-width: 1024px) {
  .contenido__texto {
    text-align: left;
  }
}

.lugares__heading {
  text-align: center;
  margin: 2rem 0 2rem 0;
}
@media (min-width: 768px) {
  .lugares__heading {
    text-align: left;
  }
}
.lugares__grid {
  display: grid;
  grid-template-columns: repeat(4, 30rem);
  column-gap: 4rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
@media (min-width: 768px) {
  .lugares__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    overflow: hidden;
  }
}
@media (min-width: 1024px) {
  .lugares__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.lugar {
  scroll-snap-align: center;
}
.lugar__imagen-contenedor {
  border-radius: 1rem;
  overflow: hidden;
}
.lugar__informacion {
  text-align: center;
}
@media (min-width: 1024px) {
  .lugar__informacion {
    text-align: left;
    width: 100%;
  }
}

.anfitrion {
  color: #FFFFFF;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  row-gap: 16rem;
  padding: 5rem;
  background-image: linear-gradient(to bottom, #000 0%, #000 50%, transparent 50%), url(../img/anfitrion.jpg);
  background-size: 100%, 60rem;
  background-position: center, center bottom;
  background-repeat: no-repeat;
}
@media (min-width: 480px) {
  .anfitrion {
    background-size: 100%, 72rem;
    background-position: center, center 27rem;
  }
}
@media (min-width: 768px) {
  .anfitrion {
    grid-template-rows: unset;
    background-image: linear-gradient(to right, #000 0%, #000 45%, transparent 55%), url(../img/anfitrion.jpg);
    background-position: 100%, 19rem top;
    padding: 12rem 3rem;
  }
}
@media (min-width: 1024px) {
  .anfitrion {
    background-position: 100%, right top;
    padding: 14rem 5rem;
  }
}
@media (min-width: 768px) {
  .anfitrion__contenido {
    max-width: 45%;
  }
}
.anfitrion__heading {
  text-shadow: 2px 2px 3px #9c9c9c;
}
.anfitrion__texto {
  color: #FFFFFF;
  margin-bottom: 0;
}
.anfitrion__link {
  background-color: #FFFFFF;
  padding: 2rem 4rem;
  color: #000;
  margin-top: 1rem;
  border-radius: 3rem;
  font-size: 2rem;
  font-weight: 700;
  display: block;
  padding: 1rem 3rem;
  text-align: center;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .anfitrion__link {
    display: inline-block;
  }
}

.experiencias__listado {
  display: grid;
  grid-template-columns: repeat(3, 30rem);
  column-gap: 2rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.experiencias__listado > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .experiencias__listado {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
    overflow: hidden;
  }
}

.experiencia__imagen {
  border-radius: 1rem;
}
.experiencia__titulo {
  font-weight: 900;
  font-size: 2rem;
}
.experiencia__descripcion {
  font-size: 1.8rem;
}

.footer {
  background-color: #e1e1e1;
  border-top: 1px solid #cccccc;
}
@media (min-width: 480px) {
  .footer__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    text-align: center;
  }
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__heading {
  margin: 1rem 0;
}
.footer__enlace {
  display: block;
  padding-bottom: 1.5rem;
  color: #484848;
}
.footer__copy {
  text-align: center;
  font-size: 1.2rem;
  text-transform: uppercase;
  color: #484848;
  margin: 0 auto;
  padding: 2rem;
  border-top: 1px solid #cccccc;
}